<template>
    <div class="accordion-item">
      <div @click="toggle" class="question">
        <h3>{{ index + 1 }}. {{ item.question }}</h3>
        <span v-if="!show">+</span>
        <span v-if="show">-</span>
      </div>
      <transition name="accordion">
        <div v-show="show" class="answer">
          <div v-html="item.answer"></div>
          <pre v-if="item.code"><code ref="codeBlock" :class="languageClass">{{ item.code }}</code></pre>
          <a v-if="item.link" :href="item.link" target="_blank">Learn more</a>
        </div>
      </transition>
    </div>
  </template>
  
  <script>
  import hljs from 'highlight.js';
  import 'highlight.js/styles/github.css';
  
  export default {
    name: 'QuestionItem',
    props: {
      item: Object,
      index: Number,
    },
    data() {
      return {
        show: false,
      };
    },
    computed: {
      languageClass() {
        return this.item.language ? `language-${this.item.language}` : '';
      },
    },
    methods: {
      toggle() {
        this.show = !this.show;
        this.$nextTick(() => {
          if (this.show && this.$refs.codeBlock) {
            hljs.highlightElement(this.$refs.codeBlock);
          }
        });
      },
    },
  };
  </script>
  
  <style>
  .accordion-item {
    margin-bottom: 10px;
    overflow: hidden;
  }
  
  .question {
    cursor: pointer;
    background: #42b983;
    padding: 15px 20px;
    border: 1px solid #3a9177;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    transition: background 0.3s ease;
    margin-bottom: -1px;
  }
  
  .question:hover {
    background: #369a76;
  }
  
  .answer {
    padding: 15px 20px;
    background: #f9f9f9;
    border: 1px solid #3a9177;
    border-radius: 0 0 4px 4px;
    margin-top: -1px;
    margin-bottom: 10px;
  }
  
  pre {
    background: #f0f0f0;
    padding: 10px;
    border-radius: 4px;
    overflow-x: auto;
  }
  
  a {
    display: inline-block;
    margin-top: 10px;
    color: #42b983;
  }
  
  .accordion-enter-active, .accordion-leave-active {
    transition: max-height 0.3s ease, padding 0.3s ease, opacity 0.3s ease;
  }
  
  .accordion-enter, .accordion-leave-to {
    max-height: 0;
    padding: 0 15px;
    opacity: 0;
    overflow: hidden;
  }
  </style>
  